import React from 'react';
import { Card, CardBody, Button, CardTitle, CardText, CardImg, CardImgOverlay } from 'reactstrap';



const Example = (props) => {
  return (
    <div>
      <Card body outline color="white">
        
        <div id="cardimg">
        <CardImg top width="100%" src={props.imgsrc} alt="Card image cap" />
        </div>
        
        
        
            <CardImgOverlay>
            <div id ="overlaytext">
            <CardText>{props.clubname}</CardText>
               <CardText>{props.subject}</CardText>
            </div>          
            </CardImgOverlay>
        
        
        
        <CardBody>
          {/* <div id ="cardtitle"> */}
          <CardTitle id="cardtitle">{props.title}</CardTitle>
          {/* </div> */}
          
          <CardText>{props.place}<br/>{props.time}</CardText>
          {/* <CardText>{props.time}</CardText> */}
        </CardBody>
        
      </Card>
      <style jax>
      {`
      div#overlaytext {
        margin-top:112px;
        padding:12px 16px 12px 16px;
        background-color:black;
        opacity:0.5;
        color:white;
      }

      div#cardimg {
          height:200px;
          overflow:hidden;
          
      }

      #cardtitle {
        color:orange;
      }

      div > div.card-body > p {
        color:gray;
      }

      #__next > div > div > div:nth-child(6) > div > div > div.card-body
      `}
      </style>
    </div>
  );
};

export default Example;


